import React, { useEffect } from "react"

export default function Header({ state }) {
	useEffect(() => {
		if (state[0] === "editor") {
			//set up editor
			document.getElementById("head").innerHTML = "  "
		} else {
			document.getElementById("head").innerHTML = "  "
		}
	})

	return (
		<header>
			<h1 id="head" className="w-max font-medium font-mono text-3xl m-5 text-center">
				  
			</h1>
		</header>
	)
}
